
#form-details{
    display: flex;
    justify-content: space-between;
    margin: 30px;
    padding: 80px;
    border: 1px solid #e1e1e1;

}
#form-details form{
    width: 65%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
#form-details form textarea,
#form-details form input{
    width: 100%;
    padding: 12px 15px;
    outline: none;
    margin-bottom:20px ;
    border: 1px solid #e1e1e1;
}

#form-details form button{

    background-color: #088178;
    color: white;

}

#form-details .people div{
    padding-bottom:  25px;
    display: flex;
    align-items: flex-start;


}

#form-details .people div img{
    width: 65px;
    height: 65px;
    object-fit: cover;
    margin-right: 15px;

}

#form-details .people div p {
    margin: 0;
    font-size: 13px;
    line-height: 25px;
}


#form-details .people div p span {
    display: block;
    font-size: 16px;
    font-weight: 600;
}

